.wrapper {
  height: 100%;
}
.wrapper-container {
  height: 100%;
}
.wrapper-content {
  height: 100%;
}
.ant-back-top {
  bottom: 40px;
  right: 20px;
}
.wrapper-content::-webkit-scrollbar {
  display: none;
}
pre {
  display: block;
  background-color: #283646 !important;
  padding: 0.5rem !important;
  overflow-y: auto;
  font-weight: 300;
  font-family: Menlo, monospace;
  border-radius: 0.3rem;
}
pre > code {
  border: 0px !important;
  background-color: #283646 !important;
  color: #FFF;
}
code {
  display: inline-block;
  background-color: #f3f3f3;
  border: 1px solid #fdb9cc;
  border-radius: 3px;
  font-size: 12px;
  padding-left: 5px;
  padding-right: 5px;
  color: #4f4f4f;
  margin: 0px 3px;
}
.list-context img {
  width: 100% ;
  border-radius: 5px;
  border: 1px solid #f0f0f0;
  max-width: 1000px !important;
  display: block;
  margin: 8px  auto ;
}
